import React, { Component } from 'react';
import { Row, Col, Form, Input, message } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import { getQueryParams, updateQueryParams } from 'utils/urlUtil';
import { BtnSearch, BtnClear, BtnExportPdf } from 'components/common/button';
import { getSemesterNo } from 'services/programDefinition';
import { SelectAjax, Switch2Value } from 'components/common';
import './style.scss';

@Form.create({ name: 'formSerchIndividual' })
class FormInfoStudent extends Component {
  state = {};
  componentDidMount = () => {
    const { form } = this.props;
    form.setFieldsValue(getQueryParams());
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      if (values.fromSemesterNo > values.toSemesterNo) {
        message.warning('T hc k ln hn n hc k');
      } else {
        updateQueryParams(values);
      }
    });
  };

  handleReset = () => {
    this.props.form.resetFields();
    updateQueryParams();
  };

  render() {
    const { getFieldDecorator, setFieldsValue } = this.props.form;
    const { disabledExport, onClickExport } = this.props;
    const optionFieldDecorator = {
      semesterId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn Hc k',
          },
        ],
      },
      trainTypeId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn H!',
          },
        ],
      },
      facultyId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn Khoa!',
          },
        ],
      },
      courseId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn Kha',
          },
        ],
      },
    };
    return (
      <Form layout="vertical" className="form-inline-label" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} optionFieldDecorator={optionFieldDecorator} />
          <Col span={5}>
            <Form.Item label="Tn SV">{getFieldDecorator('studentName')(<Input />)}</Form.Item>
          </Col>
          <Col span={5}>
            <Form.Item label="M SV">{getFieldDecorator('studentCode')(<Input />)}</Form.Item>
          </Col>
          <Col span={5}>
            <Form.Item label="T hc k">
              {getFieldDecorator('fromSemesterNo', optionFieldDecorator.semesterId)(
                <SelectAjax
                  setFieldsValue={setFieldsValue}
                  service={getSemesterNo}
                  // typeValue="int"
                />
              )}
            </Form.Item>
          </Col>
          <Col span={5}>
            <Form.Item label="n hc k">
              {getFieldDecorator('toSemesterNo', optionFieldDecorator.semesterId)(
                <SelectAjax
                  setFieldsValue={setFieldsValue}
                  service={getSemesterNo}
                  // typeValue="int"
                />
              )}
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item label="Gm im rt" className="form-item-switch">
              {getFieldDecorator('includeFailingGrades', {})(<Switch2Value />)}
            </Form.Item>
          </Col>
          <Col span={24} style={{ textAlign: 'right' }}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
              <BtnExportPdf onClick={onClickExport} disabled={disabledExport} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormInfoStudent;



